.notification-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

.notification{
    padding: 20px;
    border-radius: 10px;
    background: darkgreen;
    opacity: 0.9;
    width: 300px;
    position: fixed;
    top:200px;
    color:#fff;
    text-align: center;

}